.input {
  width: 150px;
  border: 1px solid #8a8a8a;
  border-radius: 4px;
  outline: none;
  
}

.input:focus {
  -webkit-box-shadow: 0px 0px 4px 0px rgba(0, 43, 255, 1);
  -moz-box-shadow: 0px 0px 4px 0px rgba(0, 43, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(0, 43, 255, 1);
}

.fiter_box {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 16px;
  padding: 16px;
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0.2637429971988795) 0%,
    rgba(0, 0, 0, 0.24413515406162467) 50%
  );
  margin-bottom: 40px;
}
